import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

import { Items } from '../../../providers';
import { ProfessionalMapper } from '../../../models/professional.model';
import { Professionals } from '../../../providers/professionals/professionals';

@IonicPage()
@Component({
  selector: 'page-professional-detail',
  templateUrl: 'professional-detail.html'
})
export class ProfessionalDetailPage {

  item: ProfessionalMapper;

  constructor(public navCtrl: NavController, navParams: NavParams, items: Items, private viewCtrl: ViewController,
              private professionalPrv: Professionals) {
    this.item = navParams.get('item') || items.defaultItem;
  }

  async getProfessional(id: string) {
    this.item = await this.professionalPrv.get(id).toPromise();
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

}



